.navbar {
    background-color: black;
}

.navbar-brand {
    color: white;
    font-size: 32px;

}

.navbar li {
    display: inline-block;
    color: white;
}

.navcenter {
    font-size: 32px;
    text-align: center;
}

.score {
    font-size: 24px;
}
.header {
    height: 220px;
    text-align: center;
    background-image: url(data:image/png;base64,);
    background-size: cover;
    background-repeat: no-repeat;
    padding-bottom: 20px;
}

.header h1 {
    padding-top: 35px;
    font-size: 72px;
    font-weight: bold;
    color: 	red;
    text-shadow:
    -1px -1px 0 rgb(217, 231, 16),
    1px -1px 0 rgb(217, 231, 16),
    -1px 1px 0 rgb(217, 231, 16),
    1px 1px 0 rgb(217, 231, 16);
}

.header p {
    font-size: 26px;
    color: 	red;
    text-shadow:
    -1px -1px 0 rgb(217, 231, 16),
    1px -1px 0 rgb(217, 231, 16),
    -1px 1px 0 rgb(217, 231, 16),
    1px 1px 0 rgb(217, 231, 16);
}

@media all and (max-width: 800px) { 
    .header h1 {
        padding-top: -40px;
        font-size: 60px; 
    }
    .header p {
        font-size: 24px;
    }
}

@media all and (max-width: 650px) { 
    .header h1 {
        font-size: 48px;
    }
    .header p {
        font-size: 20px;
    }
}

.card {
    height: 185px;
    position: relative;
    width: 175px;
    margin-bottom: 20px;
    box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.48);
}

.card .img-container {
    height: 100%;
    overflow: hidden;
  }

  .card .img-container > img {
    width: 100%
  }

.wrapper {
    padding-top: 50px;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
    max-width: 1200px;
    margin: auto;
}

@media all and (max-width: 1090px) { 
    .wrapper {
        max-width: 900px;
    }
    
}
